import React, { useEffect, useState } from "react"
import LoadingMask from './components/LoadingMask'
import Character from './components/Character'
import Subscription from './components/Subscription'

const App = () => {

  const [characters, setCharacters] = useState(null)
  const [subTimerDone, setSubTimerDone] = useState(false)

  const getCharacters = async () => {
    const res = await fetch("https://demoapi.com/api/series/howimetyourmother")
    const charData = await res.json()
    setCharacters(charData)
  }

  useEffect(() => {
    setTimeout(() => {
      setSubTimerDone(true)
    }, 10000)
    getCharacters()
  }, [])

  return (
    <>
      <h1>Series API</h1>

      {!characters ? <LoadingMask /> : characters.map((character) => <Character character={character} key={character.name} />)}

      {subTimerDone && <Subscription />}
    </>
  )
}

export default App  
